.navbar {
    width: 90vw;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nav {
    box-shadow: 0px 4px 4px rgba(255, 255, 255, 0.15);
}
.nav.light {
    background-color: #fff;
}

.nav.dark {
    background-color: #000;
}

.navbar__list {
    display: flex;
}
.navbar__list > li {
    margin-left: 3%;
    list-style: none;
}

.navbar__list > li:first-child {
    margin-left: 0;
}

.navbar__list.dark li a {
    color: #fff;
}

.navbar__list.light li a {
    color: #000;
}

.navbar__settings {
    display: flex;
    align-items: center;
}

.vertical-line {
    margin-left: 10%;
    margin-right: 10%;
}

.theme-icon {
    font-size: 1.5vw;
    cursor: pointer;
}
